body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{text-align:center;box-sizing:border-box}a{text-decoration:none;color:white}.userContainer{position:relative}.userContainer .remove-btn{position:absolute;bottom:10px;right:10px;z-index:2;padding:0.5rem 1rem;border:none;border-radius:20% 0 0 0;background-color:white;font-family:monospace;cursor:pointer}.userContainer .remove-btn:hover{opacity:0.8}.user{display:flex;flex-flow:column nowrap;width:200px;height:260px;margin:10px;border:1px solid white;border-radius:3%}.user img{align-self:center;width:150px;height:150px;border-radius:100%;object-fit:cover;object-position:top}.user .status{margin:auto;width:50%}.user .status #HIDE{display:none}.user .status #SOLVED{color:limegreen}.user .status #TRIED{color:darkorange}.user .status #NOT-SOLVED{color:red}.user:hover{background-image:-webkit-gradient(linear, right top, left top, from(#bdbbbe), to(#9d9ea3)),radial-gradient(88% 271%, rgba(255,255,255,0.25) 0%, rgba(254,254,254,0.25) 1%, rgba(0,0,0,0.25) 100%);background-image:linear-gradient(to left, #bdbbbe 0%, #9d9ea3 100%),radial-gradient(88% 271%, rgba(255,255,255,0.25) 0%, rgba(254,254,254,0.25) 1%, rgba(0,0,0,0.25) 100%)}

.addUserBtn{display:flex;margin:10px;flex-flow:column nowrap;width:200px;height:260px;border:1px solid white;border-radius:3%}.addUserBtn:hover{background-image:-webkit-gradient(linear, right top, left top, from(#BDBBBE), to(#9D9EA3)),radial-gradient(88% 271%, rgba(255,255,255,0.25) 0%, rgba(254,254,254,0.25) 1%, rgba(0,0,0,0.25) 100%);background-image:linear-gradient(to left, #BDBBBE 0%, #9D9EA3 100%),radial-gradient(88% 271%, rgba(255,255,255,0.25) 0%, rgba(254,254,254,0.25) 1%, rgba(0,0,0,0.25) 100%)}.addUserBtn:hover .btn{background-color:rgba(51,51,51,0.3);cursor:pointer}.addUserBtn:hover .handleInputField{color:#333}.addUserBtn .btn{color:#fff;width:150px;height:150px;font-weight:100pt;align-self:center;border-radius:100%;background-color:transparent}.addUserBtn .handleInputField{max-width:150px;height:40px;align-self:center;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue' sans-serif;font-size:13pt;font-weight:bold;outline:none;border:none;border-bottom:#fff 1px solid;background-color:transparent;color:#fff}.addUserBtn ::-webkit-input-placeholder{font-style:italic}.addUserBtn :-moz-placeholder{font-style:italic}.addUserBtn ::-moz-placeholder{font-style:italic}.addUserBtn :-ms-input-placeholder{font-style:italic}

*{text-align:center;box-sizing:border-box}body{background:#282c34;color:#fff}.App-header{display:flex;flex-wrap:wrap;justify-content:center}.App-header input{margin:0px 5px;max-width:150px;height:40px;align-self:center;text-transform:uppercase;text-align:center;font-family:monospace;font-size:35pt;outline:none;border:none;border-bottom:#fff 1px solid;background-color:transparent;color:#fff}.users-area{justify-content:space-evenly;display:flex;flex-wrap:wrap;margin-top:60px;width:auto}

